body {
  margin: 0;
  font-family: 'Lato', sans-serif;
  height: 100vh;
  width: 100vw;
  background-color: #222;
}

.container {
  position: relative;
  padding: 5%;

  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;

}

.simc-input {
  width: 100%;
}

.simc-output {
  width: 100%;
}

.convert-button, .copy-button {
  width: 300px;
  border-radius: 5px;
  background-color: #ddd;
  color: #222;
  font-size: 24px;
  margin: 0 10px;
}